import React, {Component} from 'react';
import {Navbar}  from 'react-bootstrap';
import './navigation.css';
import { Link, NavLink } from 'react-router-dom'
import Resume from '../arghas-space-stuff/ARGHA-Resume.pdf';


var main=true;
var art=false;
var vid=false;
var code=false;
class navigation extends Component{
    render(){
        return(
        <div>
            <Navbar className="NavBar">
                <Navbar.Brand className="title">Argha's Space</Navbar.Brand>
                <div className="Navlinks">
                    
                <ul>
                <li className={main ? "NavItem": "NavItem active"}  onClick={mainPage}>
                <Link style={{textDecoration:'none', color:'black'}}to="/about">
                    About
                </Link>
                </li>
                <li className={art ? "NavItem active": "NavItem"}  onClick={artPage}>
                <Link style={{textDecoration:'none', color:'black'}} to="/art">
                Art
                </Link>
                </li>
                <li className={vid ? "NavItem active": "NavItem"}  onClick={vidPage}>
                <Link style={{textDecoration:'none', color:'black'}} to="/video">
                Video
                </Link>
                </li>
                <li className={code ? "NavItem active": "NavItem"}  onClick={codePage}>
                <Link style={{textDecoration:'none', color:'black'}} to="/programming">
                Code
                </Link>
                </li>
                </ul>
                <ul className='contacts'>
                <li>
                <a style={{textDecoration:'none', color:'black'}} href="https://www.linkedin.com/in/argha-ghosh-dastidar-549a69142/">
                LinkedIn
                </a>
                </li>
                <li>
                <a style={{textDecoration:'none', color:'black'}} href={Resume}>
                CV
                </a>
                </li>
                </ul>
                </div>
            </Navbar>
        </div>
        )
    }
}
export function mainPage(){
    main=!main
    console.log("main=" + main);
}
export function artPage(){
    art=!art
    main=!main
    console.log("art="+art);
}
export function vidPage(){
    main=false;
    art=false;
    vid=true;
    code=false;
    console.log("vid="+vid);
}
export function codePage(){
    main=false;
    art=false;
    vid=false;
    code=true;
    console.log("code="+code);
}

export default navigation;

